.app-wrapper {
    width: 1000px;
    margin: 0 auto;
    display: grid;

    grid-template-areas:
    "h h"
    "n c";

    grid-template-rows: 60px 1fr;
    grid-template-columns: 3fr 10fr;
    /* grid-gap: 10px */
}

.app-wrapper-content {
    grid-area: c;
    background-color: lightseagreen;
}